
.project {
    overflow-y: scroll;
    overflow-x: hidden;
    width:100vw;
    height: 100vh;
}

.projectContainer {
    height: calc(100vh - 200px);
    width: 100%;
}

::-webkit-scrollbar {
    width: 5px;
    height: 3px;
}

::-webkit-scrollbar-track {
    background-color: white;
}

::-webkit-scrollbar-thumb {
    background-color: white;
}