
.experience {
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: calc(100vh - 200px);
    width: 100vw;
}

.experienceTop {
    display:flex;
    align-items: center;
    justify-content: center;
    height: 70%;
}


.experienceWrapper {
    display:flex;
    width: 100%;
    height: 100%;
}


.experienceLeft {
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
} 


.experienceRight {
    flex:1;
    display:flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    margin-right: 50px;
}


.techImg {
  width: 24rem;
  height: 24rem;
  object-fit: contain;
  margin-right: 30px;
}



.description {
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 18px;
}

.experienceBottom {
    display:flex;
    justify-content: center;
    position:relative;
    height: 30%;
    width: 100%;
}

.bottomWrapper {
    display:flex;
    justify-content: center;
    position:absolute;
    bottom: 10px;
}

.site {
    margin: 0 10px;
}

.address {
    color: grey;
    margin-left: 15px;
}

.address:hover {
    color:grey;
    text-decoration: none;
}
