
.center {
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.centerTop {

    width: 70%;
}


.objective {
    font-weight: bold;
    font-size: 28px;
    text-align: left;
    margin-top: 20px;
    margin-bottom: 50px;
}

.education {
    font-size: 16px;
    font-weight: 300;
    text-align: right;
    margin:0;
}

.centerBottom {
    height: 30%;
    width: 100%;
}

.bottomFooter {
    display:flex;
    align-items: center;
    justify-content: center;
    position:relative;
    height:100%;
}

.email {
    position:absolute;
    bottom: 10px;
    color: grey;
}